<template>
    <div class="personal-container">
        <div class="personal-information">
            <img :src="headerImg" alt="" class="head-portrait" @click="goToChilder(headerImg,username)">
            <span class="personal-user-name" @click="goToChilder(headerImg,username)">{{username}}</span>
            <span class="personal-home" @click="backHome">主页</span>
            <span class="personal-text" @click="goToChilder(headerImg,username)">这个人很懒，什么都没留下</span>
        </div>
        <div class="personal-columns" @click="goToChilder(headerImg,username)">
            <div><span>0</span><br/> 投稿</div>
            <div><span>{{commentCount}}</span><br/> 评论</div>
            <div><span>{{followCount}}</span><br/> 关注</div>
            <div><span>0</span><br/> 粉丝</div>
        </div>
        <div class="personal-columns-information" @click="goToSetNews"><span class="set-circular icon-xiaoxizhongxin" /> 消息设置 <span class="icon-jiantouxia"/> </div>
        <div class="personal-columns-information" @click="goToSetUser(username)"><span class="set-circular icon-zhanghaoshezhi" /> 账号设置 <span class="icon-jiantouxia"/> </div>
        <div class="personal-columns-information" @click="goToRankWealth"><span class="set-circular icon-caifu" /> 等级财富 <span class="icon-jiantouxia"/> </div>
        <div class="personal-columns-information" @click="goToHistory"><span class="set-circular icon-lishijilu" /> 历史记录 <span class="icon-jiantouxia"/> </div>
        <div class="personal-columns-information" @click="goToSetOpinion"><span class="set-circular icon-pan_icon" /> 意见反馈 <span class="icon-jiantouxia"/> </div>
        <div class="out-login" @click="OutLogin">退出登录</div>
    </div>
</template>
<script>
export default {
     props:[
        'history'
    ],
    data(){
        return{
            headerImg: this.$route.query.headerImg,
            username:"",
            followCount:0,
            commentCount:0,
        }
    },
    created() {
      this.username = JSON.parse(sessionStorage.userInfo).username
      for(let i = 0 ; i <this.history.length;i++){
            if(this.history[i].gz===true){
                 this.followCount += 1
            }
            if(this.history[i].plct===true){
                 this.commentCount += 1
            }
      }
  },
    methods:{
        OutLogin(){
            sessionStorage.removeItem("userInfo")
            this.$router.push('/login')
            setTimeout(() => {
                location.reload()
            }, 200);
            clearTimeout()
        },
        backHome(){
            this.$router.push('/')
        },
        goToChilder(headerImg,username){
            this.$router.push({
                name: 'Contribution',
                query: {
                headerImg: headerImg,
                username:username
                }
            })
        },
        goToSetUser(username){
            this.$router.push({
                name: 'SetUser',
                query: {
                username:username
                }
            })
        },
        goToHistory(){
            this.$router.push("/history")
        },
        goToSetNews(){
            this.$router.push("/setnews")
        },
        goToRankWealth(){
            this.$router.push("/rankwealth")
        },
        goToSetOpinion(){
            this.$router.push("/setopinion")
        }
    }
}
</script>

<style lang="scss" scoped>

.personal-container{
    position: fixed;
    top: 40px;
    width: 100%;
    height: 100%;
    background-color: #f7f7f7;

    .personal-information{
        position: relative;
        margin-top: 10px;
        padding: 10px 15px;
        width: 100%;
        height: 70px;
        background-color: #fff;
  
        .head-portrait {
            width: 48px;
            height: 48px;
            border-radius: 50%;
        }    
        .personal-user-name{
           position: absolute;
           top: 15px;
           left: 70px;
           font-size: 20px;
            
        }
        .personal-home{
            position: absolute;
            top: 12.5px;
            right: 15px;
            padding: 5px;
            width: 36px;
            height: 30px;
            font-size: 12px;
            color: #fb3;
            border:1px solid #fb3;
            border-radius: 2px;
        }
        .personal-text{
            position: absolute;
            top: 45px;
            left: 70px;
            color: #aaa;
            font-size: 12px;
        }
    }
    
    .personal-columns{
        margin-bottom: 10px;
        display: flex;
        justify-content: center;
        width: 100%;
        height: 50px;
        background-color: #fff;
        text-decoration: none;

        div{
            width: 25%;
            height: 100%;
            text-align: center;
            line-height: 25px;
            font-size: 0.12rem;
            color: #666;

            span{
                font-size: 14px;
                font-weight: bold;
                color: #f60;
            }
        }
    }
    .personal-columns-information{
        padding: 0 15px;
        width: 100%;
        height: 41px;
        line-height: 40px;
        font-size: 14px;
        border: 1px solid #eaeaea;
        background-color: #fff;
        color: #666;

        .set-circular{
            display: inline-block;
            margin: 8px 10px 8px  0;
            padding: 4px;
            width: 23px;
            height: 23px;
            border-radius: 50%;
        }
        
        .icon-jiantouxia{
            margin: 8px 0;
            float: right;
            font-size: 20px;
        }
        
        .icon-xiaoxizhongxin{
            background-color: #f60;
        }
        .icon-zhanghaoshezhi{
            background-color: #09c;
        }
        .icon-caifu{
            background-color: #66f;
        }
        .icon-lishijilu{
            background-color: #fb3;
        }
        .icon-pan_icon{
            background-color: #9c0;
        }
    }
    .out-login{
        margin: 10px 0;
        padding: 15px 0;
        width: 100%;
        height: 40px;
        text-align: center;
        font-size: 12px;
        color: #666;
        background-color: #fff;
    }
}
</style>
